import React from 'react';
import './ImageLinkForm.css'

const ImageLinkForm = ({onInputChange, onButtonSubmit}) => {
  return (
    <div>
      <p className='f3'>
        {'This Robot will detect faces in your pictures. Give it a try '}
      </p>
      <div className='center'>
        <div className='form center pa4 br3 shadow-5'>
          <input className='f4 pa2 w-70 center' type='tex' onChange = {onInputChange} placeholder = "Search the Url of Image to Detect"  />
          <button
          className='ButtonH w-30 grow f4 link ph3 dib white bg-light-purple'
          onClick = {onButtonSubmit}
          >Detect</button>
        </div>
      </div>
    </div>
  );
}

export default ImageLinkForm;



